import * as React from 'react'
import ReactMapGL, { Marker, NavigationControl, SVGOverlay } from 'react-map-gl';
import { INITIAL_VIEW_SET, MAPBOX_TOKEN } from '../../helper/constants';
import CoordinateJson from './data.json'
import MarkerImage from '../../assets/image/svg/marker.svg'
import { Viewport } from 'deck.gl';

export default class ReactMapGlView extends React.Component <any, any>{

    constructor(props: any) {
      super(props)
      this.state = {
        viewport: {
          latitude:  23.8103,
          longitude: 90.4125,
          zoom:      10,
          bearing:   0,
          pitch:     0
        }

      }
    }
    redraw({project}: any) {
      const [cx, cy] = project([23, 90]);
      return <circle cx={cx} cy={cy} r={4} fill="blue"/>;
    }

    render () {
      console.log(this.state.viewport)
        const COORDINATES = CoordinateJson
        return (
          <ReactMapGL
            {...this.state.viewport}
            width="75vw"
            height="50vh"
            onViewportChange={viewport => this.setState({viewport})}
            // mapStyle='mapbox://styles/razbiswas/ckj10rq0l1g4k1aruildzqtqb'
            mapboxApiAccessToken={MAPBOX_TOKEN}>
              {
                COORDINATES.map(data => {
                  return (<Marker
                   key={data.id}
                   latitude={data.geometry.coordinates[0]}
                   longitude={data.geometry.coordinates[1]}>
                      <img src={MarkerImage} style={{border: 'none'}}/>
                  </Marker>)
                })
              }

              <div style={{ position: 'absolute', bottom:20, right: 0 }}>
                <NavigationControl />
              </div>
                {/* <SVGOverlay redraw={this.redraw} /> */}
            </ReactMapGL>
        )
    }
}