import * as React from 'react'
import ReactMapGL, {  NavigationControl } from 'react-map-gl';
import { INITIAL_VIEW_SET, MAPBOX_TOKEN } from '../../helper/constants';
import DeckGL from '@deck.gl/react';
import { GeoJsonLayer, ArcLayer } from 'deck.gl';
import GL from '@luma.gl/constants';


export default class DeckGLView extends React.Component <any, any>{
  constructor(props: any) {
    super(props);
    this.state = {
      viewport: INITIAL_VIEW_SET
    }
  }

  componentDidMount() {
    this.setState({viewport: INITIAL_VIEW_SET.zoom = 1})
  }

  render() {
    // Now we are getting sample Json from outside but we will make a own JSON based on common
    const TransportData = 'https://d2ad6b4ur7yvpq.cloudfront.net/naturalearth-3.3.0/ne_10m_airports.geojson';

    const layers = [
      new GeoJsonLayer({
        id:                   'transport',
        data:                 TransportData,
        filled:               true,
        pointRadiusMinPixels: 2,
        pointRadiusScale:     2000,
        getRadius:            (f:any) =>  11 - f.properties.scalerank,
        getFillColor:         [55, 114, 223],
        pickable:             true,
        autoHighlight:        true,
      }),
      new ArcLayer({
        id:                'arcs',
        data:              TransportData,
        //                 @ts-ignore
        dataTransform:     (d: any) => d.features.filter((f: any) => f.properties.scalerank < 4),
        getSourcePosition: (f: any) => [-0.4531566, 51.4709959],
        getTargetPosition: (f: any) => f.geometry.coordinates,
        getSourceColor:    [241, 96, 73],
        getTargetColor:    [55, 114, 223],
        getWidth:          1
      })
  ];
    return (
      <ReactMapGL
       {...this.state.viewport}
       width="75vw"
       height="50vh"
       parameters={{
        blendFunc: [GL.SRC_ALPHA, GL.ONE, GL.ONE_MINUS_DST_ALPHA, GL.ONE],
        blendEquation: GL.FUNC_ADD
        }}
       onViewportChange={viewport => this.setState({viewport})}
       mapStyle='mapbox://styles/razbiswas/ckj10rq0l1g4k1aruildzqtqb'
       mapboxApiAccessToken={MAPBOX_TOKEN}>
        <DeckGL
         initialViewState={INITIAL_VIEW_SET}
         controller={true}
         pickingRadius={5}
         layers={layers}>
        </DeckGL>
        <div style={{ position: 'absolute', bottom:20, right: 0 }}>
          <NavigationControl />
        </div>
      </ReactMapGL>
    );
  }
}
