import React from 'react';
import './App.scss';
import {
  HashRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import Menu from './component/menu';
import Sidebar from './component/sidebar';
import Driver from './component/drivers';
import Warehouse from './component/warehouse';
import Warehouse2 from './component/warehouse2';
import Warehouse3 from './component/warehouse3';

class App extends React.Component {
  render() {
    return (
      <Router>
      <div className="transport-app">
        <Menu/>
        <Sidebar/>
        <Switch>
          <Route exact path="/">
            <Driver/>
          </Route>
          <Route exact path="/driver">
            <Driver />
          </Route>
          <Route exact path="/Warehouse-1">
            <Warehouse/>
          </Route>
          <Route exact path="/Warehouse-2">
            <Warehouse2/>
          </Route>
          <Route exact path="/Warehouse-3">
            <Warehouse3/>
          </Route>
          <Route exact path="/Warehouse-4">
            <Driver />
          </Route>
        </Switch>

      </div>
      </Router>
    );
  }
}

export default App;
